import React, { useState } from "react";
import { PDFDocument } from "pdf-lib";
import { saveAs } from "file-saver";
import "./App.css";

export default function PdfEditor() {
    const [pdfFile, setPdfFile] = useState(null);
    const [pageRange, setPageRange] = useState("");
    const [totalPages, setTotalPages] = useState(null);

    // 
    const handleFileChange = (event) => {
        const file = event.target.files[0];
        if (file && file.type === "application/pdf") {
            setPdfFile(file);

            const fileReader = new FileReader();
            fileReader.readAsArrayBuffer(file);
            fileReader.onload = async () => {
                const pdfBytes = fileReader.result;
                const pdfDoc = await PDFDocument.load(pdfBytes);
                setTotalPages(pdfDoc.getPageCount());
            };
        } else {
            alert("PDF");
        }
    };

    // 
    const handleDeletePages = async () => {
        if (!pdfFile || !pageRange) {
            alert("PDF");
            return;
        }

        const fileReader = new FileReader();
        fileReader.readAsArrayBuffer(pdfFile);
        fileReader.onload = async () => {
            const pdfBytes = fileReader.result;
            const pdfDoc = await PDFDocument.load(pdfBytes);
            const totalPages = pdfDoc.getPageCount();

            // 
            const [start, end] = pageRange.split("-").map(Number);
            if (isNaN(start) || isNaN(end) || start >= end || start < 1 || end > totalPages) {
                alert(" 1  " + totalPages + " ");
                return;
            }

            // 
            const pagesToRemove = Array.from({ length: end - start + 1 }, (_, i) => start + i - 1);
            pagesToRemove.reverse().forEach(pageIndex => {
                pdfDoc.removePage(pageIndex);
            });

            // PDF
            const modifiedPdfBytes = await pdfDoc.save();
            const blob = new Blob([modifiedPdfBytes], { type: "application/pdf" });

            saveAs(blob, "modified.pdf");
            alert(": PDF");
        };
    };

    return (
        <div className="container">
            <h2>PDF </h2>
            <input type="file" accept="application/pdf" onChange={handleFileChange} />
            {totalPages && <p className="total-pages">: {totalPages}</p>}
            <br />
            <input
                type="text"
                placeholder=" (: 2-4)"
                value={pageRange}
                onChange={(e) => setPageRange(e.target.value)}
            />
            <br />
            <button onClick={handleDeletePages}>PDF</button>
        </div>
    );
}
